import { dbService, storageService } from "fbase";
import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTrash,
  faPencilAlt,
  faQuestionCircle,
  faFlag,
} from "@fortawesome/free-solid-svg-icons";

const Nweet = ({ nweetObj, isOwner }) => {
  const [editing, setEditing] = useState(false);
  const [info, setInfo] = useState(false);
  const [newNweet, setNewNweet] = useState(nweetObj.text);
  const onDeleteClick = async () => {
    const ok = window.confirm("정말로 삭제하시겠습니까?");
    if (ok) {
      await dbService.doc(`nweets/${nweetObj.id}`).delete();
      if (nweetObj.attachmentUrl !== "") {
        await storageService.refFromURL(nweetObj.attachmentUrl).delete();
      }
    }
  };
  const toggleEditing = () => setEditing((prev) => !prev);
  const infoClick = () => setInfo((prev) => !prev);
  const onSubmit = async (event) => {
    event.preventDefault();
    await dbService.doc(`nweets/${nweetObj.id}`).update({
      text: newNweet,
    });
    setEditing(false);
  };
  const onChange = (event) => {
    const {
      target: { value },
    } = event;
    setNewNweet(value);
  };
  const report = () => {
    window.location.href = "https://forms.gle/oN3qrA9VzRnFpvP47";
  };
  let ifname;
  if (nweetObj.creatorName === null) {
    ifname = false;
  } else {
    ifname = true;
  }
  let guest;
  if (nweetObj.creatorEmail === null) {
    guest = false;
  } else {
    guest = true;
  }
  return (
    <div className="nweet">
      {info ? (
        <>
          <h1 className="buk">게시물 정보</h1>
          <br />
          <h5 className="createdTime">{nweetObj.createdTime}에 게시됨</h5>
          <br />
          <h5 className="createdTime">
            {nweetObj.isMobile ? <>모바일</> : <>Pc</>}에서 작성됨
          </h5>
          <br />
          <h5 className="createdTime">
            {guest ? (
              <>작성자 이메일 : {nweetObj.creatorEmail}</>
            ) : (
              <>게스트로 로그인한 사용자</>
            )}
          </h5>
          <br />
          <h5 className="createdTime">
            {nweetObj.attachmentUrl && (
              <a href={nweetObj.attachmentUrl}>이미지 주소로 바로 이동</a>
            )}
          </h5>
          <br />
          <h5 className="createdTime report" title="신고" onClick={report}>
            신고 <FontAwesomeIcon icon={faFlag} color="red" />
          </h5>
          <br />
          <span onClick={infoClick} className="formBtn cancelBtn">
            취소
          </span>
        </>
      ) : (
        <>
          {editing ? (
            <>
              <form onSubmit={onSubmit} className="container nweetEdit">
                <input
                  type="text"
                  placeholder="게시물 문구를 수정하세요."
                  value={newNweet}
                  required
                  autoFocus
                  onChange={onChange}
                  className="formInput"
                />
                <input type="submit" value="게시물 수정" className="formBtn" />
              </form>
              <span onClick={toggleEditing} className="formBtn cancelBtn">
                취소
              </span>
            </>
          ) : (
            <>
              <h5 className="creatorName">
                {ifname ? (
                  <>{nweetObj.creatorName}에게서 작성됨</>
                ) : (
                  <span Style="color:red">작성자 이름 없음</span>
                )}
              </h5>
              <br />
              <h1>{nweetObj.text}</h1>
              <div className="a">
                {nweetObj.attachmentUrl && <img src={nweetObj.attachmentUrl} />}
              </div>
              <div className="nweet__actions">
                {isOwner && (
                  <>
                    <span onClick={onDeleteClick}>
                      <FontAwesomeIcon icon={faTrash} />
                    </span>
                    <span onClick={toggleEditing}>
                      <FontAwesomeIcon icon={faPencilAlt} />
                    </span>
                  </>
                )}
                <span onClick={infoClick}>
                  <FontAwesomeIcon icon={faQuestionCircle} />
                </span>
              </div>
            </>
          )}
        </>
      )}
    </div>
  );
};

export default Nweet;
